
/*
    ===============================
            Work Styles
    ===============================
*/

import styled from 'styled-components';

export const Work_el = styled.div`
  height: auto;
  padding: 50px 0;
  overflow: hidden;
  background: #fff
`

export const Work_title = styled.h2` 
  font-size: 60px; 
`

export const Work_title_span = styled.span` 
  font-weight: normal
`

export const Work_part = styled.div` 
  margin-top: 20px;
  width: 30%;
  float: left;
  height: auto;
  padding: 100px 0;
  border: 1px solid #888;
  box-sizing: border-box;
  text-align: center;
  margin-left: 5%;
  margin-left: ${props => props.first === 1 ? '0': '5%' };

  @media (max-width: 575px) {
      width: 100%;
      float: none;
      margin-left: 0     
    }

  @media (min-width: 576px )and (max-width:768px) {
      width: 47%;
      margin-left: ${props => props.last === '1' ? '0': '5%' };
    }
`

export const Work_first = styled.div`
  margin-left: 0
`

export const Work_icon = styled.i` 
  color: #888;
  margin-bottom: 20px
`

export const Part_title = styled.h4` 
  font-size: 25px;
  color: #eb5424;
  margin-bottom: 20px
`

export const Work_line = styled.hr` 
  width: 60%;
  margin: auto;
  margin-bottom: 20px
`

export const Part_desc = styled.p` 
  font-size: 14px;
  color: #888;
  padding: 20px
`











